.myProjectsContanier {
  margin: 14rem 10% 0 10%;
  display: block;
  justify-content: center;
}

.title {
  font-size: 3rem;
  font-weight: bold;
}

.cardContainer {
  display: flex;
  padding: 1rem;
  width: 100%;
}

.cardContainer:hover {
  border: solid 1px #fff;
  margin-left: 1.4rem;
  margin-right: 1.4rem;
  transition: 0.2s;
}

.imgContainer img {
  width: 300px;
}

.projectInfo {
  margin-left: 1.2rem;
  width: 100%;
}

.projectInfo:nth-child(1) {
  text-align: center;
  font-size: 1.4rem;
  font-weight: bold;
}

.projectInfo p:nth-child(2) {
  text-align: left;
  font-weight: normal;
  font-size: 1rem;
}

.imgLogo {
  width: 80px;
  margin: 1.2rem;
}

@media (max-width: 600px) {
  .myProjectsContanier {
    margin: 0;
    padding: 2.6rem;
    display: block;
    align-items: center;
    justify-content: center;
  }
  .cardContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0rem;
    width: 100%;
  }
  .cardContainer:hover {
    border: none;
    margin-left:0;
    margin-right: 0;
    transition: 0s;
  }
  .projectInfo {
    margin-left:0rem;
    width: 100%;
  }

  .projectInfo:nth-child(1) {
    text-align: center;
    font-size: 1.4rem;
    font-weight: bold;
  }
  
  .projectInfo p:nth-child(2) {
    text-align: center;
    font-weight: normal;
    font-size: 1rem;
  }
  
}
