.aboutContainer {
  margin: 14rem 10% 0 10%;
  display: block;
  justify-content: center;
}

.dividedInfo {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.titleContainer p {
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
}

.locationContainer {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  border-right: 1.5px solid white;
  padding: 2rem;
}

.locationContainer img {
  width: 9rem;
  border-radius: 4%;
}

.locationIconContainer {
  margin-top: 2rem;
}

.myInfoAboutMe {
  margin-left: 3rem;
  margin-top: 4rem;
}

.myInfoAboutMe p {
  font-size: 1.8rem;
}

.buttonNavInfo {
  border: none;
  background-color: var(--background_color);
  color: aliceblue;
  cursor: pointer;
  width: 6rem;
  font-size: 1.2rem;
  margin: 0 0.4rem 0 0.4rem;
}

.buttonNavInfo:hover {
  color: var(--backgroun d_color);
  background-color: white;
  transition: 1s;
}

.showMyinfoOnScreen {
  word-wrap: break-word;
  width: 35rem;
}

.ipnImg {
  width: 15rem;
}

@media screen and (min-width: 600px) and (max-width: 1024px) {
  .showMyinfoOnScreen {
    word-wrap: break-word;
    width: 100%;
  }
  .aboutContainer {
    margin: 0.2rem;
  }
}

@media (max-width: 600px) {
  .aboutContainer {
    margin: 0.2rem;
  }
  .myInfoAboutMe {
    margin: 0;
  }
  .dividedInfo {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .showMyinfoOnScreen {
    word-wrap: normal;
    width: 99%;
  }
  .myInfoAboutMe p {
    font-size: 1.2rem;
    text-align: center;
  }
  .locationContainer {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    border: none;
    padding: 0rem;
  }
  .locationContainer img {
    width: 9rem;
    border-radius: 4%;
  }

  .imgContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }


  .locationIconContainer {
    margin-top: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
