
.infoContainer{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.myPhoto {
  border-radius: 50%;
  width: 350px;
  height: 350px;
  object-fit: cover;
  border: solid;
  box-shadow: 0 0 0.5rem 0.3rem white;
}

.photoContianer{
    margin-top: 4rem;
}

.infoText{
    margin: 4rem 2rem 4rem 2rem;
}

.infoText p{
    margin: 1.2rem;
}

.infoText p:nth-child(1){
    font-size: 2.7rem;
}

.infoText p:nth-child(2){
    font-size: 4rem;
    font-weight: bold;
}

.infoText p:nth-child(3){
    font-size: 2rem;
}


@media (max-width: 600px) {
    .mainPhotoContainer{
        text-align: center;
        display: flex;
        justify-content: center;
    }
    
    .infoContainer{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .myPhoto {
        border-radius: 50%;
        width: 200px;
        height: 200px;
        object-fit: cover;
        border: solid;
        box-shadow: 0 0 0.5rem 0.3rem white;
      }
      


}
